import React from "react";
import "../index.css";
import { ButtonToolbar, MenuItem, DropdownButton } from 'react-bootstrap';


class Buttons extends React.Component {

    handleSelect = (evt) => {
        this.props.gridSize(evt);
    }


    render() {
        return (
        <div className="center">
            <ButtonToolbar>
              <button className="btn btn-default" onClick={this.props.startButton}>
                Start
              </button>
              <button className="btn btn-default" onClick={this.props.stopButton}>
                Stop
              </button>
              <button className="btn btn-default" onClick={this.props.clear}>
                Clear
              </button>
              <button className="btn btn-default" onClick={this.props.slow}>
                Slow
              </button>
              <button className="btn btn-default" onClick={this.props.fast}>
                Fast
              </button>
              <button className="btn btn-default" onClick={this.props.seed}>
                Seed
              </button>
              <DropdownButton
                title="Grid Size"
                id="size-menu"
                onSelect={this.handleSelect}
                >
                <MenuItem eventKey="1">30x20</MenuItem>
                <MenuItem eventKey="2">60x40</MenuItem>
                <MenuItem eventKey="3">80x60</MenuItem>
               </DropdownButton>
            </ButtonToolbar>
        </div>    
        )
    }

}

export default Buttons